import React, { useState, useEffect } from 'react'
import { InputNumber, Table } from 'antd';

const TableMaterial = (props) => {

    const [minimumStock, setMinimumStock] = useState([])
    const [selectedRowKeys, setSelectedRowKeys] = useState([])

    useEffect(() => {
        props.minimumStockMaterial(minimumStock)
    }, [minimumStock])

    useEffect(() => {
        if (props.selectedMaterial.length > 0) {
            let newMinimumStock = [...minimumStock]
            props.selectedMaterial.map((item, index) => {
                newMinimumStock[item.id] = item.minimum_stock
                setMinimumStock(newMinimumStock)
            })
        }

    }, [props.selectedMaterial])

    const handleChangeMinimumStock = (id, val) => {
        let newMinimumStock = [...minimumStock]
        newMinimumStock[id] = val
        setMinimumStock(newMinimumStock)
    }

    //on check table item
    const onSelectChange = (selectedRowKeys) => {
        setSelectedRowKeys(selectedRowKeys)
        props.selectedMaterialDelete(selectedRowKeys)
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const columns = [
        {
            title: 'Material Code',
            dataIndex: 'material_code',
            width: 200
        },
        {
            title: 'Minimum Stock',
            dataIndex: 'id',
            render: (id) => (
                <InputNumber
                    min={0}
                    size='small'
                    readOnly={props.view}
                    value={minimumStock[id] ? minimumStock[id] : 0}
                    onChange={(id, (val) => { handleChangeMinimumStock(id, val) })} />
            ),
            width: 150
        },
        {
            title: 'Description',
            dataIndex: 'description',
            width: 300
        },
        {
            title: 'Classification',
            dataIndex: 'classification',
            render: (classification) => {
                if (classification) { return classification.name }
            },
            width: 200
        },
        {
            title: 'UoM',
            dataIndex: 'uom',
            render: (uom) => {
                if (uom) { return uom.name }
            },
            width: 100
        },
    ];
    return (
        <Table
            rowSelection={rowSelection}
            rowKey={record => record.id}
            dataSource={props.selectedMaterial}
            columns={columns}
            pagination={false}
            size='small'
            scroll={{ y: 300, x: 1000 }} />
    )
}

export default TableMaterial;