import React, { useState, useEffect } from 'react'
import { Table, Checkbox, Space, Input, Row, Col, Tag } from 'antd';
//helpers
import Api from '../../helpers/api';
import handleApiError from '../../helpers/handleApiError';

const api = new Api();
const MaterialModal = (props) => {

    const [selectedRowData, setSelectedRowData] = useState([])
    const [checkedPage, setCheckedPage] = useState([])
    const [q, setQ] = useState('')
    const [loading, setLoading] = useState(false)
    const [data, setData] = useState([])
    const [pagination, setPagination] = useState({})

    useEffect(() => {
        fetch()
    }, [q])

    useEffect(() => {
        props.materialSelected(selectedRowData)
    }, [selectedRowData])

    useEffect(() => {
        let newSelectedRowData = [...selectedRowData]
        newSelectedRowData = newSelectedRowData.filter(val => !props.selectedMaterialDelete.includes(val.id));
        setSelectedRowData(newSelectedRowData)

        if (checkedPage[pagination.current] && checkedPage[pagination.current].length != newSelectedRowData.length) {
            let newCheckedPage = [...checkedPage]
            newCheckedPage[pagination.current] = newSelectedRowData.length
            setCheckedPage(newCheckedPage)
        }

        if (props.roomId) {
            getMaterialByRoom(props.roomId)
        }
    }, [props.selectedMaterialDelete])

    const fetch = (params = {}) => {
        setLoading(true)

        api.getMaterial({ ...params, q })
            .then(response => {
                const data = response.data;

                let pagination = {}

                pagination.current = data.current_page
                pagination.total = data.total;
                pagination.pageSize = parseInt(data.per_page);

                setData(data.data)
                setPagination(pagination)
                setLoading(false)
            })
            .catch(error => {
                setLoading(false)
                handleApiError(error, props)
            })
    }

    const getMaterialByRoom = (roomId) => {
        api.viewMaterialSourcingByRoomId(roomId)
            .then(response => {
                setSelectedRowData(response.data.map(item => item.material))
            }).catch(error => {
                handleApiError(error, props)
            })
    }

    const searchData = (value) => {
        setQ(value);
    }

    const handleTableChange = (paginate, sorter) => {
        const pager = { ...pagination };
        pager.current = paginate.current;

        setPagination(pager)

        fetch({
            per_page: paginate.pageSize,
            page: paginate.current,
            sort_field: sorter.field,
            sort_order: sorter.order === 'ascend' ? 'asc' : 'desc',
        });
    }

    const handleSelect = (record, selected) => {
        if (selected) {
            setSelectedRowData((keys) => [...keys, record])
        } else {
            setSelectedRowData((keys) => {
                const index = keys.map(function (e) { return e.id; }).indexOf(record.id);
                return [...keys.slice(0, index), ...keys.slice(index + 1)];
            });
        }
    };

    const toggleSelectAll = (e) => {
        let newCheckedPage = [...checkedPage]

        let currentSelectedRows = data.map((r) => r.id);

        let currentSelectedData = data;
        let mergeSelectedData = [...selectedRowData, ...currentSelectedData]
        let uniqueData = mergeSelectedData.filter((field, index, array) => array.findIndex(item => item.id == field.id) == index);

        if (e.target.checked) {
            setSelectedRowData(uniqueData);

            newCheckedPage[pagination.current] = currentSelectedRows
            setCheckedPage(newCheckedPage)
        } else {
            uniqueData = uniqueData.filter(val => !currentSelectedRows.includes(val.id));
            setSelectedRowData(uniqueData);

            newCheckedPage[pagination.current] = []
            setCheckedPage(newCheckedPage)
        }
    };

    const headerCheckbox = (
        <Checkbox
            checked={checkedPage[pagination.current] ? checkedPage[pagination.current].length : 0}
            indeterminate={
                checkedPage[pagination.current] ? checkedPage[pagination.current].length < data.total : ''
            }
            onChange={toggleSelectAll}
        />
    );

    const rowSelection = {
        selectedRowKeys: selectedRowData.map(item => item.id),
        onSelect: handleSelect,
        columnTitle: (headerCheckbox)
    };


    const columns = [
        {
            title: 'Material Code',
            dataIndex: 'material_code',
            sorter: true,
        },
        {
            title: 'Description',
            dataIndex: 'description',
            sorter: true,
        },
        {
            title: 'Classification',
            dataIndex: 'classification',
            render: (classification) => {
                if (classification) { return classification.name }
            },
        },
        {
            title: '(UoM) Unit of Measurement',
            dataIndex: 'uom',
            render: (uom) => {
                if (uom) { return uom.name }
            },
        },
        // {
        //     title: 'Require Serial Number ?',
        //     dataIndex: 'serial_number',
        //     render: (serial_number) => (<Tag style={{ marginBottom: 3, marginTop: 3 }}>{serial_number ? 'Yes' : 'No'}</Tag>),
        //     sorter: true,
        // },
    ]
    return (
        <Space direction="vertical" size="large" style={{ width: '100%' }}>
            <Row>
                <Col md={19} />
                <Col md={5} >
                    <Input.Search placeholder="Search"
                        onSearch={searchData} />
                </Col>
            </Row>
            <Row>
                <Col lg={24} xs={24}>
                    <Table
                        loading={loading}
                        rowSelection={rowSelection}
                        columns={columns}
                        rowKey={(record) => record.id}
                        onChange={handleTableChange}
                        dataSource={data}
                        pagination={{
                            pageSizeOptions: ['20', '50', '100', '200'],
                            showSizeChanger: true,
                            showQuickJumper: true,
                            showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
                            ...pagination
                        }}
                        scroll={{ y: 300, x: 1000 }}
                    />
                </Col>
            </Row>
        </Space>
    );
}

export default MaterialModal;